.footer {
  padding-top: 50px;
  padding-bottom: 55px;

  background-color: $black-pearl;

  @media (max-width: $mobile-size-max) {
    padding-top: 35px;
  }
}

.footer__wrapper {
  display: grid;

  grid-template-columns: 250px 1fr 345px;

  @media (max-width: $tablet-size-max) {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, min-content);
  }

  @media (max-width: $mobile-size-max) {
    display: block;
  }
}

.footer__logo-social {
  @media (max-width: $tablet-size-max) {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;

    grid-column: 1 / 3;
  }

  @media (max-width: $mobile-size-max) {
    display: block;
    margin-bottom: 23px;
  }
}

.footer__logo {
  display: block;
  margin-bottom: 34px;

  @media (max-width: $tablet-size-max) {
    margin-bottom: 32px;
  }

  @media (max-width: $mobile-size-max) {
    margin: 0 auto 20px;
  }
}

.footer__social-list {
  display: grid;
  max-width: 165px;
  margin-left: 15px;

  grid-template-columns: repeat(3, auto);

  @media (max-width: $tablet-size-max) {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
  }

  @media (max-width: $mobile-size-max) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: none;
    margin-left: 0;
  }
}

.footer__social-item--pinterest {
  @media (max-width: $tablet-size-max) {
    order: -1;
  }
}

.footer__social-list li:not(:last-child) {
  @media (max-width: $tablet-size-max) {
    margin-left: 25px;
  }
}

.footer__header {
  margin-top: 0;
  margin-bottom: 21px;

  font-weight: 400;
  font-size: 15px;
  line-height: 107%;
  font-family: $ff-roboto-condensed;
  color: $color-default-white;
  text-transform: uppercase;
  letter-spacing: 0.05em;

  @media (max-width: $mobile-size-max) {
    margin-right: -25px;
    margin-bottom: 19px;
    margin-left: -25px;
    padding-top: 23px;
    padding-right: 25px;
    padding-left: 25px;

    border-top: 1px solid rgba(248, 253, 255, 0.7);
    cursor: pointer;

    &::after,
    &::before {
      content: "";
      position: absolute;
      top: 28px;
      right: 2px;

      width: 15px;
      height: 1px;

      background-color: $alice;
    }

    &::after {
      transform: rotate(90deg);
    }
  }
}

.footer__nav {
  margin-top: 7px;
  margin-left: 20%;

  @media (max-width: $tablet-size-max) {
    margin-top: 0;
    margin-left: 0;
  }

  @media (max-width: $mobile-size-max) {
    position: relative;

    // &::after,
    // &::before {
    //   content: "";
    //   position: absolute;
    //   top: 21px;
    //   right: 2px;

    //   width: 15px;
    //   height: 1px;

    //   background-color: $alice;
    // }

    // &::after {
    //   transform: rotate(90deg);
    // }
  }
}

.footer__nav-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 150px;
  margin-right: 20px;
  padding-right: 75px;

  @media (max-width: $tablet-size-max) {
    max-height: none;
  }

  @media (max-width: $mobile-size-max) {
    display: none;
  }
}

.footer__nav li {
  max-width: 220px;
  margin-bottom: 6px;
}

.footer__nav a {
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  font-family: $ff-pt-mono;
  color: $color-default-white;

  opacity: 0.7;
}

.footer__contacts {
  margin-top: 7px;

  @media (max-width: $tablet-size-max) {
    margin-top: 0;
    margin-left: 15px;
  }

  @media (max-width: $mobile-size-max) {
    position: relative;

    margin-left: 0;

    // &::after,
    // &::before {
    //   content: "";
    //   position: absolute;
    //   top: 21px;
    //   right: 2px;

    //   width: 15px;
    //   height: 1px;

    //   background-color: $alice;
    // }

    // &::after {
    //   transform: rotate(90deg);
    // }
  }
}

.footer__contacts-list {
  @media (max-width: $mobile-size-max) {
    display: none;
  }
}

.footer__contacts address {
  margin-bottom: 8px;

  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  font-family: $ff-pt-mono;
  color: $color-default-white;

  opacity: 0.7;

  @media (max-width: $mobile-size-max) {
    margin-bottom: 15px;

    font-size: 14px;
    line-height: 135%;
  }
}

.footer__contacts li {
  margin-bottom: 6px;
}

.footer__contacts a {
  margin-bottom: 12px;

  font-weight: 400;
  font-size: 16px;
  line-height: 156%;
  font-family: $ff-pt-mono;
  color: $color-default-white;

  opacity: 0.7;

  @media (max-width: $mobile-size-max) {
    font-size: 14px;
    line-height: 135%;
  }
}

.footer__nav.is-open .footer__nav-list {
  @media (max-width: $mobile-size-max) {
    display: block;
    padding-bottom: 10px;
  }
}

.footer__contacts.is-open .footer__contacts-list {
  @media (max-width: $mobile-size-max) {
    display: block;
    margin-right: -25px;
    margin-left: -25px;
    padding-right: 25px;
    padding-bottom: 10px;
    padding-left: 25px;

    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}

.footer__contacts.is-open .footer__header::after {
  @media (max-width: $mobile-size-max) {
    width: 0;
  }
}

.footer__nav.is-open .footer__header::after {
  @media (max-width: $mobile-size-max) {
    width: 0;
  }
}
